import http from '../../http-common'

class CentralCostAccountSercice {
  getAll () {
    return http.get('Central_Cost_Account')
  }

  getAllTreeAcc () {
    return http.get('Central_Cost_Account/GetAllTreeAcc')
  }

  getAllPagnation (params) {
    return http.get('Central_Cost_AccountPaging', { params })
  }

  get (id) {
    return http.get(`Central_Cost_Account/getById?Id=${id}`)
  }

  search (params) {
    return http.get('Central_Cost_Account/Search', { params })
  }

  create (data) {
    return http.post('Central_Cost_Account', data)
  }

  update (id, data) {
    return http.put(`Central_Cost_Account?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Central_Cost_Account?Id=${id}`)
  }
}

// eslint-disable-next-line new-cap
export default new CentralCostAccountSercice()
